import { GW2API_Call } from "./GW2API_Call";

export class GW2ItemFindProcess{

    apiKey: string;
    itemID: string;


    constructor(apiKey: string, itemID: string){
        this.apiKey = apiKey;
        this.itemID = itemID;
    }

    async Execute() {
        return new Promise((res,err)=>{
            let apiCall : GW2API_Call = new GW2API_Call(this.apiKey);
            // First get the character list
            apiCall.GetCharacterList().then((result)=>{
                res(apiCall.Response);
            }

            ).catch((error)=>{
                err("Error");
            }          
            );
        });
    }


}